<template>
  <main>
    <div class="row w-100 services-add-wrapper p-0 m-0">
      <div
        class="col-sm-12 col-lg-6 col-md-8 offset-lg-3 offset-md-2 offset-sm-12 services-add-form p-0"
      >
        <h5 class="text-center bg-light w-100 py-3">New Service</h5>
        <form
          @submit.prevent="addService()"
          class="p-2 mb-5"
          autocomplete="off"
          ref="serviceForm"
        >
          <div class="form-floating mb-3">
            <input
              v-model="vehicle.vehicleNo"
              type="text"
              class="form-control form-control-lg text-uppercase"
              id="vehicle-no"
              placeholder="HRXXXXXX"
              required
            />
            <label for="vehicle-no">Vehicle No</label>
          </div>
          <div class="form-floating mb-3">
            <input
              v-model="vehicle.vehicleModel"
              type="text"
              class="form-control form-control-lg"
              id="vehicle-no"
              placeholder="Model"
              required
            />
            <label for="vehicle-no">Model</label>
          </div>
          <div class="form-floating mb-3">
            <input
              v-model="vehicle.vehicleKm"
              type="number"
              class="form-control form-control-lg text-uppercase"
              id="vehicle-km"
              placeholder="KM"
              required
            />
            <label for="vehicle-km">Km</label>
          </div>
          <div class="form-floating mb-3">
            <input
              v-model="vehicle.serviceDate"
              type="Date"
              class="form-control form-control-lg text-uppercase"
              id="vehicle-service-date"
              placeholder="Service Date"
              required
            />
            <label for="vehicle-service-date">Service Date</label>
          </div>
          <div class="d-flex p-2 py-3 border mb-3">
            <p class="m-0 me-3">Service Stype :</p>
            <div class="form-group d-flex">
              <div class="form-check me-3">
                <input
                  class="form-check-input"
                  type="radio"
                  name="serviceType"
                  id="PS-service"
                  value="PS"
                  v-model="vehicle.serviceType"
                />
                <label class="form-check-label" for="PS-service"> P/S </label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="serviceType"
                  id="RR-service"
                  value="RR"
                  v-model="vehicle.serviceType"
                />
                <label class="form-check-label" for="RR-service"> R/R </label>
              </div>
            </div>
          </div>
          <template v-if="this.vehicle.vehicleNo && this.vehicle.serviceType">
            <div class="form-floating mb-3">
              <input
                v-model="vehicle.customerName"
                type="text"
                class="form-control form-control-lg"
                id="customer-name"
                placeholder="HRXXXXXX"
                required
              />
              <label for="customer-name">Customer Name</label>
            </div>
            <div class="form-floating mb-3">
              <input
                v-model="vehicle.phoneNumber"
                type="tel"
                maxlength="10"
                minlength="10"
                class="form-control form-control-lg"
                id="phone-no"
                placeholder="HRXXXXXX"
              />
              <label for="phone-no">Phone Number</label>
            </div>
            <div class="form-floating mb-3">
              <input
                v-model="vehicle.billNumber"
                type="number"
                required
                class="form-control form-control-lg"
                id="bill-no"
                placeholder="HRXXXXXX"
              />
              <label for="bill-no">Bill Number</label>
            </div>
            <div class="form-floating mb-3">
              <input
                v-model="vehicle.billAmount"
                type="number"
                class="form-control form-control-lg"
                id="bill-amount"
                placeholder="HRXXXXXX"
              />
              <label for="bill-amount">Bill Amount</label>
            </div>
            <div v-if="vehicle.billNumber" class="mb-3 p-0">
              <p class="p-0 m-2">
                <label for="bill-photo">Bill Photo Copy</label>
              </p>
              <input
                type="file"
                class="form-control form-control-lg"
                id="bill-photo"
                placeholder="HRXXXXXX"
                @change="uploadImage($event)"
              />
            </div>
          </template>
          <!-- Bill Preview -->
          <div v-if="billPreview" class="bill-preview border p-2">
            <img class="img-fluid img-thumbnail" :src="billPreview" alt="" />
          </div>
          <div
            v-if="vehicle.vehicleNo && vehicle.serviceType"
            class="d-grid fixed-bottom p-2 bg-white"
          >
            <button type="submit" class="btn btn-primary btn-lg">
              <span
                v-if="loading && !vehicle.billPhoto"
                class="spinner-border spinner-border-sm text-light"
                role="status"
              >
              </span>
              <span class="ms-3"> Add Service +</span>
            </button>
          </div>
        </form>
      </div>
    </div>
  </main>
</template>

<script>
// impomrting http for api fetch
import http from "../common/http-common";
import qs from "query-string";
// image compressor
const LZUTF8 = require("lzutf8");

export default {
  data() {
    return {
      vehicle: {
        vehicleNo: "",
        vehicleModel: "",
        vehicleKm: "",
        customerName: "",
        phoneNumber: "",
        billNumber: "",
        billAmount: "",
        billPhoto: "",
        serviceDate: "",
        serviceType: "",
      },
      loading: false,
      billPreview: "",
    };
  },
  created() {
    const query = this.$route.query;
    if (query.vehicleNo) {
      this.vehicle.vehicleNo = query.vehicleNo;
      this.vehicle.vehicleModel = query.vehicleModel;
    }
  },
  methods: {
    uploadImage(e) {
      this.loading = true;
      const image = e.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(image);
      reader.onload = (e) => {
        this.billPreview = e.target.result;
        this.vehicle.billPhoto = e.target.result;
        // compressing the uploaded image
        this.compress(this.vehicle.billPhoto);
        this.loading = false;
      };
    },
    compress(input) {
      // var encode = LZUTF8.compress(input);
      //   decompress will be used to fetch and show the image from the database
      //   var output = LZUTF8.decompress(encode);
      this.vehicle.billPhoto = input || encode;
    },
    async addService() {
      this.loading = true;
      try {
        const result = await http.post("/vehicles/addService", this.vehicle);

        if (result.data.success) {
          alert("Service Added");
          this.$router.push({
            path: "/service/" + this.vehicle.vehicleNo,
          });
          // reset form
          this.loading = false;
          this.vehicle = {};
          this.billPreview = "";
        } else {
          alert("Something went wrong!");
          // reset form
          this.loading = false;
          this.vehicle = {};
          this.billPreview = "";
        }
      } catch (error) {
        alert(error.response);
        this.loading = false;
        this.vehicle = {};
        this.billPreview = "";
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
